                                                            </v-date-picker>
                                                        </div>
                                                        <!-- :min-date="CurrentDate" -->
                                                        <span v-if="startDateError(service.planServiceId)" class="text-danger small">
                                                            {{ $t("StartDateRequired") }}
                                                        </span>
                                                    </div>

                                                </div>
                                                <div class="col-md-6 mb-2">
                                                    <div class="form-group">
                                                        <label>{{ $t("EndDate") }}</label>
                                                        <div class="input-group">
                                                            <v-date-picker mode="date" class="timedate input-group" v-model="newRateForms[service.planServiceId].effective_end_date" :min-date="newRateForms[service.planServiceId].effective_start_date ? new Date(newRateForms[service.planServiceId].effective_start_date) : null">
                                                                <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                    <input placeholder="MM/DD/YYYY" @click="togglePopover()" class="form-control" :value="inputValue" v-on="inputEvents" />
                                                                    <div @click="togglePopover()" class="input-group-append">
                                                                        <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                    </div>
                                                                </template>
                                                            </v-date-picker>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <!-- Rates Table -->
                                            <div class="listing">
                                                <div class="table-responsive">
                                                    <table class="table table-bordered dt-responsive nowrap mt-0">
                                                        <thead class="thead-bg border">
                                                            <tr>
                                                                <th style="min-width:95px;" v-if="service.service_type_name == 'Usage Based'">{{ $t("UnitFrom") }}</th>
                                                                <th style="min-width:95px;" v-if="service.service_type_name == 'Usage Based'">{{ $t("UnitTo") }}</th>
                                                                <th style="min-width:100px;">
                                                                    <template v-if="service.service_type_name == 'Support' && newRateForms[service.planServiceId].billingType === 'PERCENTAGE'">
                                                                        {{ $t("Rate") }}
                                                                    </template>
                                                                    <template v-else>
                                                                        {{ $t("Amount") }}
                                                                    </template>
                                                                </th>
                                                                <th style="min-width:100px;" class="text-center" v-if="service.service_type_name == 'Usage Based'">{{ $t("Action") }}</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(rate, ri) in newRateForms[service.planServiceId].rates" :key="ri">
                                                                <td v-if="service.service_type_name== 'Usage Based'">
                                                                    <input class="form-control" type="number" v-model.number="rate.from" min="0" />
                                                                </td>
                                                                <td v-if="service.service_type_name == 'Usage Based'">
                                                                    <input class="form-control" :class="{ 'is-invalid': serviceUnitToError(service.planServiceId, ri) }" :value="toDisplay(rate.to)" @input="rate.to = fromDisplay($event.target.value)" @blur="validateServiceUnitRange(service.planServiceId, ri)" placeholder="" type="number" />
                                                                    <!-- <span v-if="serviceUnitToError(service.planServiceId, ri)" class="text-danger small">
                                                                        {{ $t("UnitToMustBeGreaterThanUnitFrom") }}
                                                                    </span> -->
                                                                </td>
                                                                <td><input :class="['form-control', { 'is-invalid': rateError(service.planServiceId, ri) }]" type="number" v-model.number="rate.rate" step="1" min="0" :max="newRateForms[service.planServiceId].billingType === 'PERCENTAGE' ? 100 : null" @input="enforceRateLimit(service.planServiceId, ri)" />
                                                                <span v-if="rateError(service.planServiceId, ri)" class="text-danger small">
                                                                    {{ $t("RateTypeValidation") }}
                                                                </span>
                                                                </td>
                                                                <td class="text-center" v-if="service.service_type_name == 'Usage Based'">
                                                                    <a href="javascript:;" @click="ri === 0 ? addRowData(service.planServiceId,rate) : removeRowData(service.planServiceId, ri)" class="mr-2">
                                                                        <em :class="ri === 0 ? 'round-icon-small fa fa-plus bg-success float-none' : 'round-icon-small fa fa-trash bg-danger float-none'"></em>
                                                                    </a>
                                                                </td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                            <div class="text-right mt-2">
                                                <button class="btn btn-success btn-sm mt-2 mr-2" @click="saveRate(service.planServiceId)" :disabled="!isFormValid(service.planServiceId)" v-if="IsSave">{{ $t("SaveRate") }}</button>
                                                                <button class="btn btn-success btn-sm mt-2 mr-2" @click="saveRate(service.planServiceId)" :disabled="!isFormValid(service.planServiceId)" v-if="IsUpdate">{{ $t("UpdateRate") }}</button>
                                                <button class="btn btn-danger btn-sm ms-1 mt-2" @click="toggleCollapse(service.planServiceId)">{{ $t("Cancel") }}</button>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2" v-if="isRetroActive && retroactiveData">
                <a :class="card - link" style="color:#000" data-toggle="collapse" href="javascript:;">
                    <div class="conversation-date heading-border"><span>{{ $t("Retroactive") }} </span></div>
                </a>

                <div class="row alert alert-success m-0 mb-3">
                    <div class="col-md-4">
                        <strong>{{ $t("FromMonth") }} </strong>: {{ formatDate(retroactiveData.StartDate) }} to
                        {{ formatDate(retroactiveData.EndDate)
              }}
                    </div>
                    <div class="col-md-4" v-if="retroactiveData.ActionType == '1' || retroactiveData.ActionType == 1">
                        <strong>{{ $t("Action") }} </strong>: {{ $t("CreateInvoicesAffectedMonth") }}
                    </div>
                    <div class="col-md-4" v-if="retroactiveData.ActionType == '2' || retroactiveData.ActionType == 2">
                        <strong>{{ $t("Adjustment") }} </strong>: {{ $t("AdjustNextBillingCycle") }}
                    </div>

                    <div class="col-md-4 text-right single-action">
                        <a href="#"><em class="fa fa-edit mx-2 text-success" v-on:click="editRetroactiveDetail(retroactiveData)"></em></a>
                        <a href="#"><em class="fa fa-times mx-2 text-danger" v-on:click="deleteRetroactiveDetail(retroactiveData)"></em></a>
                    </div>

                </div>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-2 mt-3 p-0">
                <div class="row flex-row-reverse">
                    <div class="col-lg-6 text-right">
                        <a href="javascript:;" @click="handleSave" title="" class="btn btn-success formbtn widthhalf mr-2" data-toggle-tooltip="tooltip" data-original-title="Save"><i class="fa fa-save pr-2"></i>{{ $t("Save") }}</a>
                    </div>
                    <!-- <div class="col-lg-6 text-left"><a href="javascript:;" title="" data-toggle-tooltip="tooltip"
                @click="addRateSchedule" data-original-title="Save" class="btn btn-primary formbtn widthhalf">
                <i class="fa fa-plus pr-2"></i>{{ $t("AddRateSchedule") }}</a></div> -->
                </div>

            </div>
        </div>
    </div>
    <transition name="modal">
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block" v-if="isRetroActiveBillingSubscription">
                <div class="modal-dialog modal-dialog-centered" style="max-width: 500px;">
                    <div class="modal-content ">
                        <div class="modal-header theme-primary partition-full">
                            <h5 class="modal-title">Price Change Detection</h5>
                            <button type="button" class="close" v-on:click="closeRetroActiveBillingSubscription">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-md-12 text-center">
                                    <p class="font-weight-medium font-18 mb-4">{{ $t("BasedOnSelection") }} </p>
                                    <p class="font-weight-medium font-18">{{ $t("IsApplyNewRate") }}</p>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" @click="handleSaveNewCustomer" title="Apply to New Customers only" class="btn btn-primary btn-lg btn-block" data-toggle-tooltip="tooltip" data-original-title="Save">
                                {{ $t("ApplyForNewCustomer") }}
                            </a>
                            <a href="javascript:;" @click="handleSaveNewAndExistingCustomer" title="Apply to Both New And Existing Customers" class="btn btn-secondary btn-lg btn-block" data-toggle-tooltip="tooltip" data-original-title="Save">
                                {{ $t("ApplyBothExistingNewCustomer") }}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
    <ModalRetroactive v-if="isModalRetroactive" @retroActiveDetail="handleRetroActiveDetail" @cancelRetroActiveList="cancelRetroActiveDetail" :refCodeRetro="refCodeRetro" :retroActDetail="retroActDetail">
    </ModalRetroactive>
    <AddPeakHours v-if="isAddPeakHours" @peakHoursDetail="handlePeakHoursDetail" @updatePeakHoursDetail="handleUpdatePeakHoursDetail" :peakRateData="peakRateData" :refCode="refCode" :currentEditIndex="currentEditIndex"></AddPeakHours>
    <ModalEnableMinimumCharge v-if="isEnableMinimiumCharge" :editEnableLimitData="editEnableLimitData" :MonthApplicable="MonthApplicable" @minimumChargesDetail="handleMinimumChargesDetail" :refCodeCharge="refCodeCharge">
    </ModalEnableMinimumCharge>
</section>
</template>

<script>
import DataServices from "../../services/DataService";
import AddPeakHours from '../../components/Modals/AddPeakHours.vue';
import ModalRetroactive from '../../components/Modals/ModalRetroactive.vue';
import ModalEnableMinimumCharge from '../../components/Modals/ModalEnableMinimumCharge.vue';
export default {
    props: {
        PlanName: {
            type: String,
            required: false
        }
    },

    components: {
        AddPeakHours,
        ModalRetroactive,
        ModalEnableMinimumCharge
    },
    computed: {
        reactiveGroupedRates() {
            return this.PlanRateListing.map(item => {
                item.serviceTypes.forEach(st => {
                    st.serviceList.forEach(service => {
                        service.grouped = this.groupedRates(service.rateDetails);
                    });
                });
                return item;
            });
        }
    },
    data() {
        return {
            billingInterval: "monthly",
            billingIntervalText: "Monthly",
            billingIntervalValue: 1,
            PlanRateListing: [],
            isAddPeakHours: false,
            currentEditIndex: null,
            SortBy: "",
            SortExp: "",
            PageSize: "100000",
            isChecked: false,
            rateScheduleName: '',
            refCode: '',
            refCodeRetro: '',
            refCodeCharge: '',
            PageNumber: "1",
            isLoading: false,
            serviceTypes: [],
            serviceList: [],
            selectedBillingType: "",
            selectedBillableUnit: "",
            isModalRetroactive: false,
            isRetroActiveBillingSubscription: false,
            currencies: [],
            usageMeasurementList: [],
            rateArray: [],
            planRateId: 0,
            monthsArray: [{
                    name: "Jan",
                    value: "1"
                },
                {
                    name: "Feb",
                    value: "2"
                },
                {
                    name: "Mar",
                    value: "3"
                },
                {
                    name: "Apr",
                    value: "4"
                },
                {
                    name: "May",
                    value: "5"
                },
                {
                    name: "Jun",
                    value: "6"
                },
                {
                    name: "Jul",
                    value: "7"
                },
                {
                    name: "Aug",
                    value: "8"
                },
                {
                    name: "Sep",
                    value: "9"
                },
                {
                    name: "Oct",
                    value: "10"
                },
                {
                    name: "Nov",
                    value: "11"
                },
                {
                    name: "Dec",
                    value: "12"
                }
            ],
            peakHoursData: [],
            peakRateData: [],
            retroActDetail: null,
            previousJsonPlanListing: null,
            previousJsonApplicableOnPlanRateDetails: null,
            isRetroAcive: false,
            retroactiveData: null,
            isCheckedMinimiumCharge: false,
            ApplicableOnPlanRateDetails: {
            isCheckedApplicableForPlan: false,
            planRateType: "fixed",
            rateList: [{
                rateScheduleName: "Updated Plan",
                currencyId: "",
                rateDetails: [
                {
                    rateLimitId: null,
                    limitType: null,
                    limitFrom: null,
                    limitTo: null,
                    customerRateType: null,
                    startDate: null,
                    effective_start_date: null,
                    endDate: null,
                    effective_end_date: null,
                    billing_type: "FLAT",
                    rates: [
                    {
                        from: 0,
                        to: Infinity,
                        rate: "0",
                        invalidFrom: false
                    }
                    ],
                    isCurrent: true
                }
                ]
            }]
            },
            MonthApplicable: [],
            editEnableLimitData: {},
            enableLimitData: [],
            serviceNameList: '',
            rateId: 0,
            isFirstClick: true,
            isEnableMinimiumCharge: false,
            addPeakHourPopupData: {
                serviceId: null,
                rIndex: null,
                stIndex: null,
                sIndex: null
            },
            saveClicked: false,
            currId: '',
            collapseStates: {},
            collapseStatesPlan: {},
            newRateForms: {},
            newRateFormsPlan: {},
            CurrencyCode: 'PKR',
            IsSave: true,
            IsUpdate: false,
            CurrentDate: new Date()
        };
    },
    mounted() {
        this.PlanRateListing.forEach(plan => {
            plan.serviceTypes.forEach(serviceType => {
                serviceType.serviceList.forEach(service => {
                    this.$set(this.collapseStates, service.planServiceId, false);
                    if (!this.newRateForms[service.planServiceId]) {
                        this.$set(this.newRateForms, service.planServiceId, {
                            billing_type: service.billing_type,
                            effective_start_date: null,
                            effective_end_date: null,
                            rates: service.rateDetails[0] ?.rates.map(r => ({
                                ...r
                            })) || [{
                                from: 0,
                                to: null,
                                rate: 0
                            }, ],
                        });
                    }
                });
            });
        });
    },
    created: async function () {
        var vm = this;
        vm.PlanId = parseInt(this.$route.params.id);
        await vm.GetDDLUsageMeasurement();
        await vm.FetchRecords();
        await this.GetDDLCurrencyData();
        await this.GetConfigurationValueByKey();
        if (this.PlanRateListing && this.PlanRateListing.length > 0) {
            if (this.PlanRateListing[0].currencyId == null) {
                this.PlanRateListing[0].currencyId = vm.currId;
            }
            this.PlanRateListing[0].rateScheduleName = this.PlanRateListing[0].rateScheduleName == null ? vm.PlanName : this.PlanRateListing[0].rateScheduleName;
        }
        vm.PlanRateListing[0].serviceTypes.forEach(item => {
            if (item.serviceTypeName === 'Support') {
                if (item.serviceList[0].billingType === 'FLT') {
                    item.serviceList[0].billingType = 'PERCENTAGE';
                } else {}
            }
        });
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
        // Clear end date when start date changes for plan-based rates
        clearPlanEndDate(index) {
            if (this.newRateFormsPlan[index]) {
                this.$set(this.newRateFormsPlan[index], 'effective_end_date', null);
            }
        },
        // Clear end date when start date changes for service-based rates
        clearServiceEndDate(planServiceId) {
            if (this.newRateForms[planServiceId]) {
                this.$set(this.newRateForms[planServiceId], 'effective_end_date', null);
            }
        },
        async GetConfigurationValueByKey() {
            var vm = this;
            var params = 'key=Currency'
            await DataServices.GetConfigurationValueForCompanyByKey(params).then((response) => {
                vm.currId = response.data;
            });
        },
        blockEKey(event) {
            const key = event.key.toLowerCase();
            if (key === 'e') {
                event.preventDefault(); // Prevent the "e" key from being typed
            }
        },
        validateInput(event, field, rate) {
            let value = event.target.value;
            value = value.replace(/[eE]/g, ''); // Remove 'e' or 'E'
            value = value.replace(/[^0-9.]/g, '');
            const decimalCount = (value.match(/\./g) || []).length;
            if (decimalCount > 1) {
                value = value.replace(/\.(?=[^\.]*$)/, ''); // Only keep the first decimal point
            }
            rate[field] = value;
        },
        savePlanRateDetail: async function () {
            let vm = this;
            debugger;
            var str = {
                planId: this.PlanId,
                isCheckedApplicableForPlan: this.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan,
                planRateType: this.ApplicableOnPlanRateDetails.planRateType,
                rateList: JSON.stringify(this.ApplicableOnPlanRateDetails.rateList)
            }
            const validateRateList = this.ApplicableOnPlanRateDetails.rateList;
            for (let i = 0; i < validateRateList.length; i++) {
                const rateSchedule = validateRateList[i];
                if (!rateSchedule.rateScheduleName || !rateSchedule.currencyId) {
                    let errorMessage = '';
                    if (!rateSchedule.rateScheduleName && !rateSchedule.currencyId) {
                        // errorMessage = `Rate schedule name and currency cannot be empty`;
                        errorMessage = vm.$t("ratescheduleorcurrencyvalidation");
                    } else if (!rateSchedule.rateScheduleName) {
                        // errorMessage = `Rate Schedule Name cannot be empty`;
                        errorMessage = vm.$t("rateschedulevalidation");
                    } else if (!rateSchedule.currencyId) {
                        // errorMessage = `Currency cannot be empty`;
                        errorMessage = vm.$t("currencycannotempty");
                    }
                    vm.ShowAlert(errorMessage, "danger", true, vm.$t("Alert"));
                    return;
                }
            }
            const currencyIds = validateRateList.map(rateSchedule => rateSchedule.currencyId);
            if (currencyIds.length > 1) {
                const uniqueCurrencyIds = new Set(currencyIds);
                if (uniqueCurrencyIds.size === 1) {

                    // vm.ShowAlert("Currency's cannot be the same.", "danger", true, vm.$t("Alert"));
                    vm.ShowAlert(vm.$t("currencycannotsame"), "danger", true, vm.$t("Alert"));
                    return;
                }
            }

            const parsedRateList = JSON.parse(str.rateList);
            for (let i = 0; i < parsedRateList.length; i++) {
                const rateListItem = parsedRateList[i];
                if (rateListItem.rateDetails && rateListItem.rateDetails[0] && Array.isArray(rateListItem.rateDetails[0].rates)) {
                    const rates = rateListItem.rateDetails[0].rates;
                    for (let j = 0; j < rates.length; j++) {
                        const rate = rates[j];
                        if (rate.rate === null || rate.rate < 0) {
                            vm.ShowAlert(vm.$t("baseratenullvalidation"), "danger", true, vm.$t("Alert"));
                            // vm.ShowAlert("Base Rate cannot be null, zero, or negative", "danger", true, vm.$t("Alert"));
                            return;
                        }
                    }
                }
            }
            // if (str.planRateType == 'slab') {
            //     const parsedRateList = JSON.parse(str.rateList);
            //     const rates = parsedRateList[0].rateDetails[0].rates;
            //     const lastRate = rates[rates.length - 1];
            //     if (lastRate && (lastRate.from === "" || lastRate.from === 0 || lastRate.from === null)) {
            //         lastRate.invalidFrom = true;
            //         vm.ShowAlert(vm.$t("consumptionfromvalidation"), "danger", true, vm.$t("Alert"));
            //         // vm.ShowAlert("Consumption From cannot be null or zero", "danger", true, vm.$t("Alert"));
            //         return;
            //     } else {
            //         lastRate.invalidFrom = false;
            //     }
            // }
            if (vm.rateId > 0) {
                if (vm.previousJsonApplicableOnPlanRateDetails) {
                    const modifiedString = JSON.stringify(this.ApplicableOnPlanRateDetails.rateList);
                    const isEqual = JSON.stringify(vm.previousJsonApplicableOnPlanRateDetails) === JSON.stringify(modifiedString);
                    if (isEqual) {
                        vm.ShowAlert(vm.$t("nochangesrates"), "danger", true, vm.$t("Alert"));
                        // vm.ShowAlert("There are no changes in rates","danger",true,vm.$t("Alert"));
                    } else {
                        vm.isRetroActiveBillingSubscription = true;
                    }
                }
            } else {
                await DataServices.SaveRateOnBehalfOfPlan(str).then((response) => {
                    if (response.data.status == "Success" && response.data.code == "1") {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Rate")]), "Success", true, vm.$t("Alert"));
                        setTimeout(() => {
                            vm.FetchRecords();
                        }, 500);
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Rate")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                })
            }
        },
        onChangePlanRateType: function () {
            const isFixed = this.ApplicableOnPlanRateDetails.planRateType === 'fixed';
            const toValue = isFixed ? Infinity : null;
            this.ApplicableOnPlanRateDetails.rateList = [{
                rateScheduleName: "",
                currencyId: this.currId,
                rateDetails: [{
                    rateLimitId: null,
                    limitType: null,
                    limitFrom: null,
                    limitTo: null,
                    customerRateType: null,
                    startDate: null,
                    effective_start_date: null,
                    endDate: null,
                    effective_end_date: null,
                    billing_type: isFixed ? 'FLAT' : '',
                    rates: [{
                        from: 0,
                        to: toValue,
                        rate: 0,
                        invalidFrom: false
                    }],
                    isCurrent: true
                }]
            }];
        },
        addNewSlabForSubscriptionPlanRate: function (rt) {
            const lastRate = rt.rateDetails[0].rates[rt.rateDetails[0].rates.length - 1];
            var newFrom = "";
            if (lastRate.to === Infinity) {
                newFrom = lastRate.from;
            } else if (lastRate.to > 1) {
                newFrom = lastRate.to + 1;
            } else if (lastRate.to >= 0 && lastRate.to < 1) {
                newFrom = lastRate.to + 0.01;
            }
            if (lastRate.to === Infinity) {
                lastRate.to = 0;
            }
            const newRate = {
                from: newFrom,
                to: Infinity,
                rate: 0,
                invalidFrom: false
            };

            rt.rateDetails[0].rates.push(newRate);
        },
        deletePlanRateSchedule: function (rt, index) {
            this.PlanRateListing.splice(index, 1);
        },
        deleteSubscriptionRateSchedule: function (rt, index) {
            this.ApplicableOnPlanRateDetails.rateList.splice(index, 1);
        },
        deleteSubscriptionPlanRate: function (rt, index) {
            rt.rateDetails[0].rates.splice(index, 1);
        },
        FunctionApplicableForPlan: async function () {
            var vm = this;
            this.ApplicableOnPlanRateDetails.rateList = [{
                rateScheduleName: "",
                currencyId: this.currId,
                rateDetails: [{
                    rates: [{
                        from: 0,
                        to: Infinity,
                        rate: 0,
                        invalidFrom: false
                    }]
                }]
            }];
            this.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan = !this.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan;
            this.ApplicableOnPlanRateDetails.rateList[0].rateScheduleName = this.ApplicableOnPlanRateDetails.rateList[0].rateScheduleName == '' ? vm.PlanName : this.ApplicableOnPlanRateDetails.rateList[0].rateScheduleName;
            //await this.FetchRecords(true);
        },
        monthNames() {
            if (!this.MonthApplicable) return '';
            return this.monthsArray
                .filter(month => this.MonthApplicable.includes(month.value))
                .map(month => month.name)
                .join(", ");
        },
        handleEdit(item) {
            this.editEnableLimitData = {};
            this.editEnableLimitData = item;
            this.refCodeCharge = 'Update';
            this.isEnableMinimiumCharge = true;
        },
        handleDelete(index) {
            this.enableLimitData = [];
            this.isCheckedMinimiumCharge = false;
        },
        checkForEnableMinimiumCharge(event) {
            this.isCheckedMinimiumCharge = event.target.checked;
            this.isEnableMinimiumCharge = this.isCheckedMinimiumCharge;
        },
        deleteRateDetailsWithLimit: function (RateDetails, index) {
            if (RateDetails.rateDetails.length > 1) {
                RateDetails.rateDetails.splice(index, 1);
            }
        },
        editRetroactiveDetail: function (data) {
            var vm = this;
            vm.retroActDetail = data;
            vm.refCodeRetro = 'Update';
            vm.isModalRetroactive = true;
        },
        addNewRateLimit: function (row, index) {
            row.rateDetails.splice(0, 0, {
                rateLimitId: row.rateDetails.length,
                startDate: "",
                effective_start_date: "",
                effective_end_date: "",
                billing_type: "",
                endDate: "",
                limitType: "",
                limitFrom: "",
                limitTo: "",
                rates: [{
                    from: 0,
                    to: Infinity,
                    rate: 0.0,
