        FetchRecords: async function (isChangedManually) {
            var vueObj = this;
            vueObj.PlanRateListing = [];
            vueObj.serviceTypes = [];
            vueObj.serviceList = [];
            vueObj.isLoading = true;

            var url = `planId=${vueObj.PlanId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;

            await DataServices.GetPlanRateListing(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    let rsltData = response.data.result;

                    // Handling ApplicableOnPlanRateDetails
                    if (rsltData.is_rate_applicable_on_plan == "1") {
                        vueObj.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan = true;
                        vueObj.ApplicableOnPlanRateDetails.planRateType = rsltData.rate_type;
                        vueObj.rateId = rsltData.subscriptionBillingData.length > 0 ? rsltData.subscriptionBillingData[0].plan_rate_id : null;
                        vueObj.ApplicableOnPlanRateDetails.rateList = [];
                        let ratelist = [];

                        rsltData.subscriptionBillingData.forEach(function (element) {
                            let obj = {
                                rateScheduleName: element.rate_schedule_name,
                                currencyId: element.currency_id,
                                rateDetails: [{
                                    rates: []
                                }]
                            };
                            let checkIfExists = ratelist.find(x => x.currencyId == element.currency_id);
                            debugger;
                            if (!checkIfExists) {
                                let rateItems = rsltData.subscriptionBillingData.filter(x => x.currency_id == element.currency_id && x.effective_start_date != null && x.effective_start_date !== '');
                                const isFixed = rsltData.rate_type === 'fixed';
                                if (rateItems.length > 0) {
                                    // Group by effective_start_date
                                    const groupedByDate = {};
                                    rateItems.forEach(item => {
                                        const dateKey = item.effective_start_date;
                                        if (!groupedByDate[dateKey]) {
                                            groupedByDate[dateKey] = {
                                                rates: [],
                                                endDate: item.effective_end_date
                                            };
                                        }
                                        groupedByDate[dateKey].rates.push({
                                            from: item.from,
                                            to: item.to,
                                            rate: item.rate,
                                            invalidFrom: false
                                        });
                                    });
                                    const rateDetails = Object.keys(groupedByDate).map(dateKey => ({
                                        rateLimitId: null,
                                        limitType: null,
                                        limitFrom: null,
                                        limitTo: null,
                                        customerRateType: null,
                                        startDate: null,
                                        effective_start_date: dateKey,
                                        endDate: groupedByDate[dateKey].endDate, // From item
                                        effective_end_date: groupedByDate[dateKey].endDate,
                                        billing_type: isFixed ? 'FLAT' : 'FLT',
                                        rates: groupedByDate[dateKey].rates,
                                        isCurrent: false // Set in groupedRates
                                    }));

                                    // Sort by date descending and set first as current
                                    rateDetails.sort((a, b) => new Date(b.effective_start_date) - new Date(a.effective_start_date));
                                    if (rateDetails.length > 0) {
                                        rateDetails[0].isCurrent = true;
                                    }

                                    obj.rateDetails = rateDetails;
                                } else {
                                    // Default single if no items
                                    const billingType = isFixed ? 'FLAT' : '';
                                    obj.rateDetails = [{
                                        rateLimitId: null,
                                        limitType: null,
                                        limitFrom: null,
                                        limitTo: null,
                                        customerRateType: null,
                                        startDate: null,
                                        effective_start_date: null,
                                        endDate: null,
                                        effective_end_date: null,
                                        billing_type: billingType,
                                        rates: [{
                                            from: 0,
                                            to: isFixed ? Infinity : null,
                                            rate: 0,
                                            invalidFrom: false
                                        }],
                                        isCurrent: true
                                    }];
                                }
                                ratelist.push(obj);
                            }
                        });

                        vueObj.ApplicableOnPlanRateDetails.rateList = ratelist;
                        vueObj.previousJsonApplicableOnPlanRateDetails = JSON.stringify(vueObj.ApplicableOnPlanRateDetails.rateList);
                    }

                    // Handling data records
                    if (rsltData.data && rsltData.data.length > 0) {
                        var row = rsltData.data;
                        vueObj.planRateId = row[0].plan_rate_id;

                        // Handle RETROACTIVE_DATA
                        let retroActive = rsltData.RETROACTIVE_DATA && rsltData.RETROACTIVE_DATA.length > 0 ?
                            rsltData.RETROACTIVE_DATA[0][0] :
                            null;
                        if (retroActive) {
                            vueObj.retroactiveData = retroActive;
                            vueObj.isRetroActive = true;
                        }

                        // Handle ENABLE_MINIMUM_CHARGE_DATA
                        let enableCharge = rsltData.ENABLE_MINIMUM_CHARGE_DATA && rsltData.ENABLE_MINIMUM_CHARGE_DATA.length > 0 ?
                            rsltData.ENABLE_MINIMUM_CHARGE_DATA[0] :
                            null;
                        if (enableCharge) {
                            vueObj.MonthApplicable = enableCharge.type_value.split(',');
                            vueObj.serviceNameList = enableCharge.map(item => item.service_names).join(", ");
                            vueObj.enableLimitData = enableCharge;
                            vueObj.isCheckedMinimiumCharge = true;
                        }

                        vueObj.serviceTypes = vueObj.serviceTypes || [];
                        vueObj.serviceList = vueObj.serviceList || [];

                        // Grouping logic
                        let servicesMap = new Map();

                        row.forEach(function (record) {
                            const serviceId = record.service_id;
                            if (!servicesMap.has(serviceId)) {
                                servicesMap.set(serviceId, {
                                    planRateId: record.plan_rate_id,
                                    planServiceId: record.plan_service_id,
                                    isPeakHoursEnabled: record.peak_rates != null && record.peak_rates.length > 0,
                                    serviceInterval: record.billing_interval || "monthly",
                                    billingIntervalValue: "1",
                                    billingType: record.billing_type,
                                    billableUnit: record.billing_unit_id || "",
                                    serviceId: record.service_id,
                                    serviceName: record.service_name,
                                    service_type_name: record.service_type_name,
                                    peakRates: record.peak_rates || [],
                                    rateDetails: []
                                });
                            }
                            let service = servicesMap.get(serviceId);

                            const effectiveDate = record.effective_start_date || '';  // Default to empty string if null
                            const billingType = record.billing_type || 'none';

                            // Only add rate details if effective_start_date is not null or empty string
                            if (effectiveDate !== null && effectiveDate !== '') {
                                let rateDetail = service.rateDetails.find(rd =>
                                    (rd.effective_start_date || 'no_date') === effectiveDate &&
                                    (rd.billing_type || 'none') === billingType
                                );

                                if (!rateDetail) {
                                    rateDetail = {
                                        rateLimitId: record.rate_limit_id,
                                        limitType: record.limit_value_type,
                                        limitFrom: record.limit_from,
                                        limitTo: record.limit_to,
                                        customerRateType: record.customer_rate_type,
                                        startDate: record.start_date,
                                        effective_start_date: record.effective_start_date,
                                        endDate: record.end_date,
                                        effective_end_date: record.effective_end_date,
                                        billing_type: record.billing_type,
                                        rates: [],
                                        isCurrent: false // We'll set this later
                                    };
                                    service.rateDetails.push(rateDetail);
                                }

                                rateDetail.rates.push({
                                    from: record.from != null ? record.from : 0,
                                    to: record.to != null ? record.to : null,
                                    rate: record.rate != null ? record.rate : 0
                                });
                            }
                        });

                        // After filtering, you can proceed with the rest of the logic...


                        // Assign isCurrent based on latest effective_start_date
                        servicesMap.forEach(service => {
                            service.rateDetails.forEach(rd => {
                                rd.isCurrent = false;
                            });
                            service.rateDetails.sort((a, b) => {
                                const dateA = a.effective_start_date ? new Date(a.effective_start_date) : new Date(0);
                                const dateB = b.effective_start_date ? new Date(b.effective_start_date) : new Date(0);
                                return dateB - dateA;
                            });
                            if (service.rateDetails.length > 0) {
                                service.rateDetails[0].isCurrent = true;
                            }
                        });

                        vueObj.PlanRateListing = [{
                            currencyId: row.length > 0 ? row[0].currency_id : vm.currId,
                            rateScheduleName: row.length > 0 ? row[0].rate_schedule_name || vueObj.PlanName : vueObj.PlanName,
                            serviceTypes: [{
                                serviceTypeName: "Services",
                                serviceList: Array.from(servicesMap.values())
                            }]
                        }];
                        
                        vueObj.previousJsonPlanListing = JSON.stringify(vueObj.PlanRateListing);
                        vueObj.isLoading = false;
                    } else {
                        vueObj.PlanRateListing = [];
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.PlanRateListing = [];
                    vueObj.isLoading = false;
                }
            });
        },

        handleIntervalChange: function (service) {
            switch (service.serviceInterval) {
                case "monthly":
                    service.billingIntervalValue = "1";
                    break;
                case "quarterly":
                    service.billingIntervalValue = "3";
                    break;
                case "yearly":
                    service.billingIntervalValue = "12";
                    break;
                default:
                    service.billingIntervalValue = "1";
                    break;
            }
        },
        addRow(row, index) {
            var vm= this;
            vm.IsUpdate= false;
            vm.IsSave= true;
            const lastRate = row.rates[row.rates.length - 1];
            // const newFrom =
            //   lastRate.to === Infinity ? lastRate.from : lastRate.to + 1;
            var newFrom = "";
            if (lastRate.to === Infinity) {
                newFrom = lastRate.from;
            } else if (lastRate.to > 1) {
                newFrom = lastRate.to + 1;
            } else if (lastRate.to >= 0 && lastRate.to < 1) {
                newFrom = lastRate.to + 0.01;
            }
            if (lastRate.to === Infinity) {
                lastRate.to = 0;
            }
            const newRate = {
                from: newFrom,
                to: Infinity,
                rate: 0,
            };
            row.rates.push(newRate);
        },
        removeRow(row, index) {
            if (row.rates.length > 1) {
                row.rates.splice(index, 1);
            }
        },
        getJSONData() {
            var vm = this;
            vm.refCodeRetro = "Insert";
            vm.retroActDetail = null;
            const data = {
                PlanRateListing: vm.PlanRateListing.map(schedule => ({
                    currencyId: schedule.currencyId,
                    rateScheduleName: schedule.rateScheduleName,
                    serviceTypes: schedule.serviceTypes.map(serviceType => ({
                        serviceTypeName: serviceType.serviceTypeName,
                        serviceList: serviceType.serviceList.map(service => {
                            const peakHours = vm.peakHoursData.find(peak => peak.serviceId === service.service_id);
