                                peakHoursList: peakHours ? vm.peakHoursData : [],
                            };
                        }),
                    })),
                })),
                chargeDetails: {
                    EnableMinimiumChargeData: vm.enableLimitData,
                    MonthApplicable: vm.MonthApplicable
                },
                RetroActiveData: vm.retroactiveData
            };
            return JSON.stringify(data);
        },
        retroSaveData(customerExisting) {
            var vm = this;
            if (vm.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan == true) {
                let rateListObj = JSON.parse(JSON.stringify(this.ApplicableOnPlanRateDetails.rateList));
                    // Get current date in ISO format
                    const currentDate = new Date().toISOString();

                    rateListObj.forEach(rateSchedule => {
                    if (rateSchedule.rateDetails && Array.isArray(rateSchedule.rateDetails)) {
                        rateSchedule.rateDetails.forEach(detail => {
                       if (detail.effective_start_date === null || detail.effective_start_date === undefined) {
                            detail.effective_start_date = currentDate;
                        }
                        });
                    }
                    });
                    var str = {
                    planId: this.PlanId,
                    isCheckedApplicableForPlan: this.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan,
                    planRateType: this.ApplicableOnPlanRateDetails.planRateType,
                    rateList: JSON.stringify(rateListObj),  // stringify the updated rateListObj here
                    applyToExistingCustomer: customerExisting
                    };
                DataServices.SaveRateOnBehalfOfPlan(str).then((response) => {
                    if (response.data.status == "Success" && response.data.code == "1") {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Rate")]), "Success", true, vm.$t("Alert"));
                        setTimeout(() => {
                            vm.FetchRecords();
                        }, 500);
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Rate")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                })
            } else {
                //const jsonData = vm.getJSONData();
                const jsonDataString  = vm.getJSONData();
                const updatedJsonString = JSON.parse(jsonDataString);
                const currentDate = new Date().toISOString();

                updatedJsonString.PlanRateListing.forEach(plan => {
                plan.serviceTypes.forEach(serviceType => {
                    serviceType.serviceList.forEach(service => {
                    if (Array.isArray(service.rateDetails) && service.rateDetails.length === 0) {
                        // Replace empty rateDetails array with default object
                        service.rateDetails = [{
                        rateLimitId: null,
                        limitType: null,
                        limitFrom: null,
                        limitTo: null,
                        customerRateType: null,
                        startDate: null,
                        effective_start_date: currentDate,
                        endDate: null,
                        effective_end_date: null,
                        billing_type: "",
                        rates: [{ from: 0, to: null, rate: 0 }],
                        isCurrent: true
                        }];
                    }
                    });
                });
                });

                // If you want to convert it back to a string:
                const jsonData  = JSON.stringify(updatedJsonString);
                const obj = JSON.stringify(jsonData);
                const requestPayload = {
                    objData: obj,
                    planId: vm.PlanId,
                    applyToExistingCustomer: customerExisting
                };
                DataServices.SavePlanRate(requestPayload).then((response) => {
                    if (response.data != null) {
                        if (response.data.Status == "Success" && response.data.Code == "1") {
                            vm.ShowAlert(
                                vm.$t("UpdatedSuccess", [vm.$t("Rate")]),
                                "Success",
                                true,
                                vm.$t("Alert")
                            );
                            setTimeout(() => {
                                vm.FetchRecords();
                            }, 500);
                            vm.isLoading = false;
                        } else {
                            vm.ShowAlert(
                                vm.$t("ErrorWhileSaving", [vm.$t("Rate")]),
                                "danger",
                                true,
                                vm.$t("Alert")
                            );
                            vm.isLoading = false;
                        }
                    }
                });
            }
        },
        closeRetroActiveBillingSubscription() {
            this.isRetroActiveBillingSubscription = false;
            //this.retroSaveData("no"); 
        },
        handleSaveNewCustomer() {
            this.isRetroActiveBillingSubscription = false;
            this.retroSaveData("no");
        },
        handleSaveNewAndExistingCustomer() {
            this.isRetroActiveBillingSubscription = false;
            this.retroSaveData("yes");
        },
        validateRates(data) {
            // Loop through the PlanRateListing and check the rates in service list
            for (let rate of data.PlanRateListing) {
                for (let serviceType of rate.serviceTypes) {
                    for (let service of serviceType.serviceList) {
                        for (let rateDetail of service.rateDetails) {
                            for (let rateObj of rateDetail.rates) {
                                // If rate is null or empty, return false
                                if (rateObj.rate === null || rateObj.rate === "" || rateObj.rate === "0" || rateObj.rate <= 0) {
                                    return false;
                                }
                            }
                        }
                    }
                }
            }
            return true; // All rates are valid
        },
        handleSave() {
            var vm = this;
            const jsonDataString  = vm.getJSONData();
            const updatedJsonString = JSON.parse(jsonDataString);
                const currentDate = new Date().toISOString();

                updatedJsonString.PlanRateListing.forEach(plan => {
                plan.serviceTypes.forEach(serviceType => {
                    serviceType.serviceList.forEach(service => {
                    if (Array.isArray(service.rateDetails) && service.rateDetails.length === 0) {
                        service.rateDetails = [{
                        rateLimitId: null,
                        limitType: null,
                        limitFrom: null,
                        limitTo: null,
                        customerRateType: null,
                        startDate: null,
                        effective_start_date: currentDate,
