                    // Stringify the modified data
                    const validateRateList = parsedData.PlanRateListing;
                    for (let i = 0; i < validateRateList.length; i++) {
                        const rateSchedule = validateRateList[i];
                        if (!rateSchedule.rateScheduleName || !rateSchedule.currencyId) {
                            let errorMessage = '';
                            if (!rateSchedule.rateScheduleName && !rateSchedule.currencyId) {
