        saveRate(planServiceId) {
            const vm = this;
            if (!this.validateForm(planServiceId)) return;

            const service = this.findServiceByPlanServiceId(planServiceId);
            if (!service) return;

            const form = this.newRateForms[planServiceId];
            const newStart = new Date(form.effective_start_date);
            const newEnd = form.effective_end_date ? new Date(form.effective_end_date) : null;
            // Improved overlap check with better null handling (ongoing rates)
            const hasOverlap = service.rateDetails.some(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail && detail === form.originalRateDetail;
                if (isCurrentEditing) return false;

                const existingStart = detail.effective_start_date ? new Date(detail.effective_start_date) : null;
                const existingEnd = detail.effective_end_date ? new Date(detail.effective_end_date) : null;
                const newStart = form.effective_start_date ? new Date(form.effective_start_date) : null;
                const newEnd = form.effective_end_date ? new Date(form.effective_end_date) : null;

                if (!existingStart || !newStart) return false;

                // Normalize to date strings (UTC start of day)
                const existingStartStr = existingStart.toISOString().split('T')[0];
                const existingEndStr = existingEnd ? existingEnd.toISOString().split('T')[0] : 'ongoing';
                const newStartStr = newStart.toISOString().split('T')[0];
                const newEndStr = newEnd ? newEnd.toISOString().split('T')[0] : 'ongoing';

                // Overlap if new range intersects existing range
                // Handle 'ongoing' as infinite future
                const newStartBeforeExistingEnd = newEndStr === 'ongoing' || (!existingEndStr || newStartStr <= existingEndStr);
                const existingStartBeforeNewEnd = existingEndStr === 'ongoing' || (!newEndStr || existingStartStr <= newEndStr);
                const rangesOverlap = newStartStr <= existingEndStr && existingStartStr <= newEndStr;

                return rangesOverlap && newStartBeforeExistingEnd && existingStartBeforeNewEnd;
            });

            // Note: This aligns with backend expectations in SavePlanRate/SaveRateOnBehalfOfPlan, where overlapping dates are rejected server-side if not caught here.



            if (hasOverlap) {
                vm.ShowAlert(
                    `${vm.$t("The date range for")} ${service.serviceName} ${vm.$t("service overlaps for existing rate.")}`,
                    "warning",
                    vm.$t('Alert')
                );
                return;
            }

            // Update previous rates with null end dates
            service.rateDetails.forEach(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail && detail === form.originalRateDetail;
                if (!isCurrentEditing && (detail.effective_end_date === null || detail.effective_end_date === undefined)) {
                    detail.effective_end_date = form.effective_start_date;
                }
            });

            const rateDetail = {
                rateLimitId: null,
                limitType: null,
                limitFrom: null,
                limitTo: null,
                customerRateType: null,
                startDate: null,
                effective_start_date: form.effective_start_date,
                endDate: null,
                effective_end_date: form.effective_end_date,
                billing_type: form.billingType,
                rates: form.rates.map(r => ({
                    ...r
                })),
                isCurrent: true
            };

            service.rateDetails.forEach(d => d.isCurrent = false);

            if (form.isEdit && form.originalRateDetail) {
                const idx = service.rateDetails.findIndex(d => d === form.originalRateDetail);
                if (idx !== -1) service.rateDetails[idx] = rateDetail;
            } else {
                service.rateDetails.push(rateDetail);
            }

            this.resetNewRateForm(planServiceId);
            this.toggleCollapse(planServiceId);
        },

