            }

            // Check rates
            if (form.rates && !form.rates.every(r => r.rate >= 0 && r.rate != null)) {
                isValid = false;
            }
            if (this.ApplicableOnPlanRateDetails.planRateType === 'slab' && form.rates) {
                for (let i = 0; i < form.rates.length; i++) {
                   
                    // Check UnitTo validation
                    if (this.planUnitToError(index, i)) {
                        isValid = false;
                        break;
                    }
                }
            }

            // For slab: check sequential
            if (this.ApplicableOnPlanRateDetails.planRateType === 'slab' && form.rates) {
                for (let i = 1; i < form.rates.length; i++) {
                    if (form.rates[i].from <= form.rates[i - 1].to || form.rates[i].from < 0) {
                        isValid = false;
                        break;
                    }
                }
            }

            return isValid;
        },

        isPlanFormValid(index) {
            return this.validatePlanForm(index, false);
        },

        validatePlanField(index, field) {
            this.validatePlanForm(index, true);
        },
         enforceRateLimit(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            const currentRate = form.rates[ri];
            if (form.billingType === 'PERCENTAGE' && currentRate.rate > 100) {
                currentRate.rate = null;
            }
            if (currentRate.rate < 0) {
                currentRate.rate = 0;
            }
        },

        onBillingTypeChange(planServiceId) {
            const form = this.newRateForms[planServiceId];
            if (form.billingType === 'PERCENTAGE') {
                form.rates.forEach(rate => {
                    rate.rate = null;
                });
            }
        },

        planUnitToError(index, ri) {
            const form = this.newRateFormsPlan[index];
            if (!form || !form.formSubmitted) return false;
            const rate = form.rates[ri];
            if (rate.to === null || rate.to === undefined || rate.to === Infinity) return false;
            return rate.to < rate.from;
        },
        validatePlanUnitRange(index, ri) {
            const form = this.newRateFormsPlan[index];
            if (form) {
                form.formSubmitted = true;
            }
        },

        serviceUnitToError(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            if (!form || !form.formSubmitted) return false;
            const rate = form.rates[ri];
            // Allow null, undefined, infinity, or equal values - only prevent UnitTo < UnitFrom
            if (rate.to === null || rate.to === undefined || rate.to === Infinity) return false;
            return rate.to < rate.from;
        },

        validateServiceUnitRange(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            if (form) {
                form.formSubmitted = true;
            }
        }
    },
};
</script>

